// src/components/ARView.js
import React, { useRef, useEffect } from 'react'
import * as THREE from 'three'
import OnirixSDK from '@onirix/ar-engine-sdk'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'

export default function ARView() {
  const canvasRef = useRef(null)

  useEffect(() => {
    let sdk, renderer, scene, camera, model
    const loader = new GLTFLoader()

    async function init() {
      // 1 Set up Three.js
      const canvas = canvasRef.current
      renderer = new THREE.WebGLRenderer({ canvas, alpha: true, antialias: true })
      renderer.setSize(window.innerWidth, window.innerHeight)
      scene    = new THREE.Scene()
      camera   = new THREE.PerspectiveCamera()

      // 2 Configure Onirix for world (floor+wall) tracking
      sdk = await OnirixSDK.configure({
        apiKey: 'YOUR_ONIRIX_API_KEY', //  replace with your key
        canvas,
        renderer,
        camera,
        tracking: 'world',             // Surface/WorldTracking mode
      })

      // 3 Load your model and add it to the scene
      loader.load('/models/tree.glb', (gltf) => {
        model = gltf.scene
        scene.add(model)

        // 4 On each AR frame, position & orient the model on whichever plane (floor or wall) is hit
        sdk.on('frame', ({ hitTest }) => {
          if (!hitTest) return
          model.position.copy(hitTest.position)
          model.quaternion.copy(hitTest.orientation)
        })
      })

      // 5 Start the render loop
      const animate = () => {
        requestAnimationFrame(animate)
        sdk.updateFrame()           // advances SLAM & hittests
        renderer.render(scene, camera)
      }
      animate()
    }

    init()

    //  Clean up when unmounting
    return () => {
      if (sdk)      sdk.dispose()
      if (renderer) renderer.forceContextLoss()
    }
  }, [])

  // Fullscreen canvas
  return (
    <canvas
      ref={canvasRef}
      style={{ width: '100vw', height: '100vh', display: 'block' }}
    />
  )
}
